.toolbar {
  height: 20px;
  width: 100%;
  text-transform: uppercase;
  background-color: #343434;
  justify-content: center;
  align-items: center;
  font-size: .5rem;
  display: flex;
  position: relative;
}

.toolbar__title {
  height: -moz-fit-content;
  height: fit-content;
}

.toolbar .dot__wrapper {
  gap: 4px;
  display: flex;
  position: absolute;
  top: 6px;
  left: 8px;
}

.toolbar .dot {
  width: 8px;
  height: 8px;
  background-color: #686866;
  border-radius: 100px;
}

.portfolio__button {
  width: 50px;
  height: 50px;
  border: 1px solid #ffffff1a;
  border-radius: 200px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  gap: 13.843px;
  transition: background-color .15s ease-in;
  display: flex;
}

.portfolio__button-case {
  -webkit-backdrop-filter: blur(16px);
  backdrop-filter: blur(16px);
  z-index: 1000;
  background-color: #10101029;
  position: fixed;
  top: 50px;
  left: 10vw;
}

.portfolio__button-case:hover {
  cursor: pointer;
  background: #ffffff29;
}

@media (width <= 860px) {
  .portfolio__button-case {
    top: 25px;
    left: 16px;
  }
}

@media (width <= 400px) {
  .portfolio__button {
    width: 35px;
    height: 35px;
    border: 1px solid #ffffff1a;
    border-radius: 200px;
    flex-shrink: 0;
    justify-content: center;
